@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

:root{
  --primary-white:#fff;
  --primary-orange:#f9ab00;
  --primary-dark:rgba(255,255,255,0);
  --primary-red:#ef4035;
  --primary-hover-red:#f33501;
  --primary-purple:#bc1cd1;
  --primary-grey:#343a40;
  --primary-light-grey:#EEEEEE;
}

body{
  font-family: "Roboto Condesed" sans-serif;
}
.App {
  text-align: center;
}

html{
  scroll-behavior: smooth;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  } 
}
/*******************Navbar********************************************************/

nav{
  z-index: 2;
}
.logo{
  width:8.25rem;

}
/**************Menu List******************/
.navbar-nav{
  font-weight: 500;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}
nav ul li{
  text-transform: uppercase;

}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color:var(--primary-white) !important;
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-band{
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

@media(max-width:768px){
  .navbar-band{
    width: 7rem;
  }
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus{
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover{
  border-top: 0.1875rem solid var(--primary-orange);
}
/*********************************Header********************************************************/
.header-wraper{
  position: relative;
  background: url(josh.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}
.main-info{
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Roboto Condesed" sans-serif;
  z-index: 1;
}
.main-info h1{
  color: var(--primary-orange);
  text-transform: uppercase;
}
@media(max-width:768px){
  .header-wraper{
    width: 73vh;
  }
}


/*********************************Typed Text********************************************************/
.typed-text{
  font-size: 2rem;
  color: var(--primary-white);
}

@media(max-width:768px){
  .main-info h1{
    font-size: 1.7rem !important;

  }
  .typed-text{
    font-size: 1.3rem !important;
  }
}

/*********************************Header Main offer********************************************************/

.btn-main-offer{
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: o;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover{
  text-decoration: none;
  background-color: var(--primary-hover-red);
  color: var(--primary-white);
  transition: .2s ease-in-out;
}
/*********************************Particle js********************************************************/

.tsparticles-canvas-el{
  position: absolute;
  height: 100vh;
  width: 100vh;
  z-index: 1;
}
@media(max-width:768px){
  .particles-canvas{
    display: none;
  }
}
/*********************************Page Header********************************************************/

.pageheader-container{
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
}
.pageheader-container hr {
  position: absolute;
  left: 0;
  width: 124px;
  border: 1px solid var(--primary-red);
}

.pageheader-container h1 {
  color: var(--primary-grey);
  text-transform: uppercase;
  border-bottom: 0.1875rem solid var(--primary-orange);
  
}

@media(max-width:768px){
  .pageheader-container hr{
    display: none;
  }
  .pageheader-container h1 {
    align-items: center;
    
  }
  
}

/*********************************About********************************************************/

.about-wraper{
  position: relative;
  height: auto;
  background-color: var(--primary-white);
  padding-bottom: 5%;
  padding-top: 2em;
}

.about-container{
  display: flex;
  
}

.about-text{
  width: 50%;
  margin-left: 10%;
}

.about-text h2{
  font-weight: 900;
  font-size: 2.875rem;
}

.about-photo{
  width: 50%;
  display: flex;
  justify-content: center;
}

.about-me{
  height: 21.25rem;
  border-radius: 10%;

}

@media(max-width:768px){
  .about-wraper{
    width: 73vh;
  }
  .about-container {
    display: block;
  }
  .about-text {
    width: 95%;
    margin:10px 10px 10px 10px;
    text-align: justify;
  }
  .about-text h2{
    text-align: center;
  }
  .about-photo{
    width: auto;
  }
}

/*********************************Services********************************************************/
.service-wraper{
  position: relative;
  height: auto;
  background-color: var(--primary-light-grey);
  padding-bottom: 5%;
  padding-top: 2em;
}
.card{
  width: 20rem;
  margin-left: 10%;
  border-bottom: 0.1875rem solid var(--primary-orange)!important;
  
}
.card-img-top{
  width: 19%!important;
  margin-left: 40%;
  margin-top: 3%;
}

.card-body{
  text-align:-webkit-center;
}

.card-body .service-text{
  margin-left: 8%;
}

.card:hover{
  background-color:var(--primary-orange);
  color: var(--primary-white);
  border-bottom: 0.1875rem solid var(--primary-grey)!important;
}

.service-container{
  display: flex;
}

@media(max-width:768px){
  .service-wraper{
    width: 73vh;
  }
  .service-container {
    display: block;
  }
  .card {
    margin-bottom: 20px;
    margin-left: 23%;
  }
}

/*********************************Experience********************************************************/

.experience-wraper{
  position: relative;
  height: auto;
  background-color: var(--primary-white);
  padding-bottom: 5%;
  padding-top: 2em;
}

.timeline-container{
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 2.5rem 0;
}
.timeline-container::after{
  background-color: var(--primary-red);
  position: absolute;
  content: '';
  left: calc(50% - 0.125rem);
  width: 0.25rem;
  height: 100%;
}

.timeline-item{
  display: flex;
  justify-content: flex-end;
  padding-right: 1.875rem;
  margin: 0.625rem 0;
  width: 50%;
}

.timeline-item:nth-child(odd) {
  align-self: flex-end;
  justify-content: flex-start;
  padding-right: 0rem;
  padding-left: 1.875rem;
}
.timeline-item-content{
  background-color: var(--primary-white);
  border-radius: 0.313rem;
  box-shadow: 0 0 0.313rem rgba(0, 0, 0, 0.3);
  max-width: 70%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
  width: 25rem;
  padding: 0.938rem;
  text-align: right;
}

.timeline-item-content::after{
  content: '';
  background-color: var(--primary-white);
  box-shadow: 0.063rem -0.063rem 0.063rem rgba(0, 0, 0, 0.2);
  transform: rotate(45deg);
  right: -0.469rem;
  top: calc(50% - 0.469rem);
  position: absolute;
  width: 0.938rem;
  height: 0.938rem;
}

.timeline-item:nth-child(odd) .timeline-item-content {
  align-items: flex-start;
  text-align: left;
}

.timeline-item:nth-child(odd) .timeline-item-content::after {
  right: auto;
  box-shadow: -0.063rem 0.063rem 0.063rem rgba(0, 0, 0, 0.2);
  left: -0.469rem;
}

.timeline-item-content .time{
  color: var(--primary-light-grey);
  font-size: 0.875rem;
}

.timeline-item-content p{
  font-size: 1rem;
  line-height: 1.5rem;
  margin: 0.938rem 0;
  max-width: 15.625rem;
}

.timeline-item-content .circle{
  background-color: var(--primary-grey);
  border: 0.188rem solid var(--primary-hover-red);
  border-radius: 50%;
  position: absolute;
  right: -2.5rem;
  z-index: 100;
  width: 1.25rem;
  height: 1.25rem;
  top: calc(50% - 0.625rem);
}

.timeline-item:nth-child(odd) .timeline-item-content .circle{
  right: auto;
  left: -2.5rem;
}

@media only screen and (max-width:767px){
  .experience-wraper{
    width: 73vh;
  }
  .timeline-item-content,
  .timeline-item:nth-child(odd) .timeline-item-content{
    padding: 15px 10px;
    text-align: center;
    align-items: center;
  }
  .timeline-item-content time{
    margin-top: 20px;
  }
  .timeline-item-content p{
    text-align: center;
    align-items: center;
  }
}

/*********************************Contact********************************************************/
.contact-wraper{
  position: relative;
  height: auto;
  background-color: var(--primary-grey);
  padding-bottom: 2%;
  padding-top: 2em;
  display: flex;
  align-items: center;
}

.contact-wraper .ContactIcons{
  width: 50%;
  display: flex;
  justify-content: center;
}
.contact-wraper .contact-text{
  width: 50%;
  display: flex;
  color: #EEEEEE;
  justify-content: center;
}

.contact-wraper .ContactIcons:hover{
  cursor: pointer;
}

@media(max-width:768px){
  .contact-wraper{
    width: 73vh;
  }
}

/*********************************Contact Us********************************************************/
.contactus-wraper{
  position: relative;
  height: auto;
  background-color: var(--primary-light-grey);
  padding-top: 2em;
  padding-bottom: 5%;
}
@media(max-width:768px){
  .contactus-wraper{
    width: 73vh;
  }
}